/* ------------------------------------------------------------ *\
	Android OS fixes
\* ------------------------------------------------------------ */

$breakpoint-mobile {
	.android-os .apps { bottom: 12%; }
}

@media only screen and ( max-width: 768px ) and ( orientation: landscape ) {
	.android-os body { font-size: 12px !important; line-height: 1.25; }

	.android-os h1,
	.android-os h2 { font-size: $font-size-base; }
}
