/* ------------------------------------------------------------ *\
	fullpage.js theme
\* ------------------------------------------------------------ */

.fp-section.fp-table.section-mobile-only { display: none; }
.fp-section.fp-table.section.section-mobile-only { display: table; }

.fp-tableCell { padding: 20px; }

#fp-nav.left { top: 224px; left: 58px; margin-top: 0 !important; }

#fp-nav ul li { width: 14px; height: 14px; margin: 11px 0; }
#fp-nav ul li a span { width: 10px; height: 10px; top: 0; left: 0; right: 0; bottom: 0; margin: auto; background: $color-inactive; }
#fp-nav ul li a span:before { content: ''; width: 6px; height: 6px; display: none; position: absolute; top: 0; left: 0; right: 0; bottom: 0; margin: auto; background-color: $color-action; border-radius: 50%; }

#fp-nav ul li:hover a span,
#fp-nav ul li a:hover span { width: 14px; height: 14px; background: $color-active; margin: auto; }

#fp-nav ul li a.active span,
#fp-nav ul li a.active:hover span,
#fp-nav ul li:hover a.active span { width: 14px; height: 14px; margin: auto; background: $color-active; }

#fp-nav ul li a.active span:before { display: block; }

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	#fp-nav.left { top: 193px; left: 40px; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	#fp-nav.left { left: 51px; }

	.fp-viewing-0 .footer,
	.fp-viewing-4 .footer { display: block; }
}

/* Mobile */

$breakpoint-mobile {
	#fp-nav { display: none; }

	.fp-viewing-0 .footer,
	.fp-viewing-4 .footer { display: none; }

	.fp-tableCell { padding: 10px; }
}
