/* ------------------------------------------------------------ *\
	Section
\* ------------------------------------------------------------ */

.section-cols,
.section-col,
.section-table,
.section-table-inner { height: 100%; }

.section-table,
.section-table-inner { width: 100%; }

.section-cols { overflow: hidden; }
.section-col { width: 50%; float: left; }

.section-scroll { width: 48px; height: 36px; position: absolute; bottom: 37px; left: 50%; z-index: 4; padding: 0; border: 0; margin-left: -24px; background: none; border-radius: 0; box-shadow: none; -webkit-appearance: none; -moz-appearance: none; appearance: none; transition: bottom .4s ease; }
.section-scroll:hover { bottom: 30px; }

.section-content { padding: 209px 59px 130px 89px; }
.section-content h2 { margin: -10px 0 5px; }
.section-content p { line-height: 1.5; max-width: 340px; }

.section-content h1,
.section-content h2 { line-height: 1.5; }

.section-media { opacity: 0; visibility: hidden; transform: translateX(50%); transition: opacity .5s ease .5s, visibility .5s ease .5s, transform .5s ease .5s; }

.section-list { background-color: $color-light; }

.section-table { display: table; }
.section-table-inner { display: table-cell; vertical-align: middle; }

.section.completely .section-media { opacity: 1; visibility: visible; transform: translateX(0%); transition-delay: 0s; }

.section-mobile-only { display: none; }
.section.section-mobile-only { display: table; }

.section .logo { position: static; top: auto; left: auto; right: auto; display: none; }

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	.section-scroll { bottom: 26px; }

	.section-content { padding: 177px 62px 130px 52px; }
	.section-content h2 { margin-top: -2px; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	.section-col { width: auto; height: 50%; float: none; }

	.section-content { padding: 177px 100px 130px 79px; }

	.section-media,
	.section-list { transform: translateY(50%); }

	.section.completely .section-media,
	.section.completely .section-list { transform: translateY(0%); }
}

/* Mobile */

$breakpoint-mobile {
	.section-scroll { display: none; }
	.section-list { background-color: transparent; }

	.section-content { padding: 20px 20px 0 20px; }
	.section-content p { max-width: none; line-height: 1.25; }

	.section-content-separator { position: relative; }
	.section-content-separator:after { content: ''; height: 1px; display: block; position: absolute; bottom: 0; left: 20px; right: 20px; background-color: #a4a4a4; }

	.section .logo { display: block; }
	.section address { padding-top: 40px; border-top: 1px solid #a4a4a4; margin-top: 40px; }
	.section address p { margin: 0; }
}

/* Smaller Mobile */

$breakpoint-mobile-smaller {
	.section-content { padding: 10px 10px 0; }

	.section-content h1,
	.section-content h2,
	.section-content p { line-height: 1.25; }

	.section-cols-adjust .section-content p { font-size: 12px; margin-bottom: 5px; }
}

@media only screen and ( max-width: 320px ) and ( max-height: 400px ) {
	.section .logo { padding: 0 0 10px; }
	.section-content { padding: 10px 10px 0; }
}
