/* ------------------------------------------------------------ *\
	Apps
\* ------------------------------------------------------------ */

.apps { width: 309px; height: 582px; position: absolute; bottom: 13.85%; left: 50%; z-index: 4; padding: 71px 23px 111px 61px; margin-left: -183px; background: url(images/temp/app.png) no-repeat 0 0; background-size: 100%; -webkit-background-size: 100%; transform: translateY(200%) rotateZ(0deg); transition: margin .7s ease-out, transform .7s ease-out; transform-origin: 50% 50%; pointer-events: none; }
.apps-visible { transform: translateY(0%) rotateZ(0deg); }
.apps-rotated { margin-left: -142px; transform: translateY(0%) rotateZ(-90deg); }

.apps-inner { position: absolute; top: 71px; left: 61px; right: 23px; bottom: 111px; overflow: hidden; }
.apps-list { height: 100%; transform: translateX(0%); transition: transform .7s ease .2s; }

.app { height: 100%; }
.app img { width: 100%; display: block; }

/* Small Desktop */

$breakpoint-small-desktop {
	.apps { width: 245px; height: 461px; padding: 56px 19px 88px 49px; margin-left: -139px; }
	.apps-rotated { margin-left: -100px; }

	.apps-inner { top: 57px; left: 49px; right: 19px; bottom: 88px; }
}

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	.apps { left: 75%; bottom: 18%; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	.apps { width: 309px; height: 582px; padding: 71px 23px 111px 61px; bottom: 50%; margin: 0 0 -25px -163px; }
	.apps-visible { transform: translateY(50%) rotateZ(0deg); }
	.apps-rotated { transform: translateY(60%) rotateZ(-90deg); margin-left: -326px; }

	.apps-inner { top: 71px; left: 61px; right: 23px; bottom: 111px; }
}

/* Mobile */

$breakpoint-mobile {
	.apps { width: 180px; height: 339px; left: 50%; bottom: 0%; padding: 41px 13px 65px 36px; margin: 0 0 0 -100px; }
	.apps-visible { transform: translateY(0%) rotateZ(0deg); }
	.apps-rotated { transform: translateY(-33%) rotateZ(-90deg); margin-left: -76px; }

	.apps-inner { top: 41px; left: 36px; right: 13px; bottom: 65px; }
}

/* Small Mobile */

$breakpoint-mobile-small {
	.apps { width: 150px; height: 283px; bottom: 0%; padding: 34px 11px 55px 30px; margin-left: -81px; }
	.apps-rotated { transform: translateY(-34.5%) rotateZ(-90deg); margin-left: -65px; }

	.apps-inner { top: 34px; left: 30px; right: 11px; bottom: 55px; }
}

/* Smaller Mobile */

$breakpoint-mobile-smaller {
	.apps { width: 130px; height: 245px; padding: 30px 10px 47px 26px; margin-left: -74px; }
	.apps-rotated { transform: translateY(-22%) rotateZ(-90deg); margin-left: -56px; }

	.apps-inner { top: 30px; left: 26px; right: 10px; bottom: 47px; }
}

@media only screen and ( max-width: 320px ) and ( max-height: 400px ) {
	.apps { bottom: -10px; }
	.apps-rotated { transform: translateY(-15%) rotateZ(-90deg); margin-left: -56px; }
}

@media only screen and ( max-width: 768px ) and ( orientation: landscape ) {
	.apps { display: none; }
}
	
/* Retina */

$retina {
	.apps { background-image: url(images/temp/app@2x.png); background-size: 100%; -webkit-background-size: 100%; }
}
