/* ------------------------------------------------------------ *\
	Footer
\* ------------------------------------------------------------ */

.footer { font-size: 14px; width: 49%; width: calc(50% - 20px); position: absolute; bottom: 20px; left: 20px; z-index: 3; padding: 0 0 33px 41px; background-color: $background-base; }
.footer p { margin: 0; }
.footer p + p { margin-top: 8px; }

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	.footer { padding: 0 0 19px 20px; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	.footer { width: 99%; width: calc(100% - 40px); display: none; bottom: 50%; padding-left: 31px; margin-bottom: 10px; }
}
