.ico-chevron-down { background: url(../css/images/sprite/ico-chevron-down.png) no-repeat 0 0; width: 48px; height: 36px; display: inline-block; vertical-align: middle; font-size: 0; }
.ico-gangverk { background: url(../css/images/sprite/ico-gangverk.png) no-repeat 0 0; width: 29px; height: 29px; display: inline-block; vertical-align: middle; font-size: 0; }
.ico-logo { background: url(../css/images/sprite/ico-logo.png) no-repeat 0 0; width: 201px; height: 29px; display: inline-block; vertical-align: middle; font-size: 0; }

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
	.ico-chevron-down { background: url(../css/images/sprite/ico-chevron-down@2x.png) no-repeat 0 0; width: 48px; height: 36px; background-size: 48px 36px; display: inline-block; vertical-align: middle; font-size: 0; }
	.ico-gangverk { background: url(../css/images/sprite/ico-gangverk@2x.png) no-repeat 0 0; width: 29px; height: 29px; background-size: 29px 29px; display: inline-block; vertical-align: middle; font-size: 0; }
	.ico-logo { background: url(../css/images/sprite/ico-logo@2x.png) no-repeat 0 0; width: 201px; height: 29px; background-size: 201px 29px; display: inline-block; vertical-align: middle; font-size: 0; }
}
