/* ------------------------------------------------------------ *\
	List
\* ------------------------------------------------------------ */

[class^="list-"] { list-style: none outside none; }

/* ------------------------------------------------------------ *\
	List Clients
\* ------------------------------------------------------------ */

.list-clients { max-width: 519px; margin: auto; transform: translateX(-3px); }
.list-clients img { width: 100%; height: auto; display: block; }
.list-clients li { width: 200px; float: left; padding: 23px 0; }
.list-clients li:nth-child(even) { float: right; }

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	.list-clients { padding: 0 30px; transform: none; }
}

/* Mobile */

$breakpoint-mobile {
	.list-clients { padding: 0 20px; }
	.list-clients li { width: 50%; padding: 5px 20px; }
}

@media only screen and ( max-width: 768px ) and ( orientation: landscape ) {
	.list-clients li { width: 25%; padding: 5px; }
}
