/* ------------------------------------------------------------ *\
	Wrapper
\* ------------------------------------------------------------ */

.wrapper { position: relative; z-index: 1; }

.wrapper-frame:before,
.wrapper-frame:after,
.wrapper-frame-inner:before,
.wrapper-frame-inner:after { content: ''; position: absolute; z-index: 2; background-color: $color-light; }

.wrapper-frame:before,
.wrapper-frame-inner:before { height: 20px; left: 0; right: 0; }

.wrapper-frame:after,
.wrapper-frame-inner:after { width: 20px; top: 0; bottom: 0; }

.wrapper-frame:before { top: 0; }
.wrapper-frame-inner:before { bottom: 0; }

.wrapper-frame:after { left: 0; }
.wrapper-frame-inner:after { right: 0; }

.wrapper-fullsize { background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; }

/* Mobile */

$breakpoint-mobile {
	.wrapper-frame:before, 
	.wrapper-frame-inner:before { height: 10px; }

	.wrapper-frame:after,
	.wrapper-frame-inner:after { width: 10px; }
}
