/* ------------------------------------------------------------ *\
	Logo
\* ------------------------------------------------------------ */

.logo { width: 49%; width: calc(50% - 20px); position: absolute; top: 20px; left: 20px; z-index: 3; padding: 41px 0 41px 41px; background-color: $background-base; }
.logo i { display: block; }

/* Tablet Landscape */

$breakpoint-tablet-landscape {
	.logo { padding-left: 20px; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	.logo { width: 99%; width: calc(100% - 40px); padding-left: 31px; }
}

/* Mobile */

$breakpoint-mobile {
	.logo { width: calc(100% - 20px); display: none; top: 10px; left: 10px; padding: 10px 0 25px; }
}
