/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

body { font-family: $font-work-sans; font-size: $font-size-base !important; line-height: $line-height-base; color: $color-base; font-weight: 400; font-style: normal; text-transform: none; text-decoration: none; letter-spacing: 0; word-spacing: 0; min-width: 320px; background: $background-base; }

a { color: $color-action; text-decoration: none; }
a:hover { text-decoration: underline; }

a[href^="tel"],
a[href^="tel"]:hover { text-decoration: none; }

h1,
h2 { font-size: calc(1.625 * $font-size-base); }

h3 { font-size: calc(1.5 * $font-size-base); }
h4 { font-size: calc(1.25 * $font-size-base); }
h5 { font-size: $font-size-base; }
h6 { font-size: calc(0.875 * $font-size-base); }

h1,
h2,
h3,
h4,
h5,
h6 { font-family: $font-raleway; color: $color-title; font-weight: 400; margin-bottom: calc($(line-height-base)em/2); }

p,
ul,
ol,
dl,
table,
blockquote { margin-bottom: $(line-height-base)em; }

h1[class],
h2[class],
h3[class],
h4[class],
h5[class],
h6[class],
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
p:last-child,
ul:last-child,
ol:last-child,
dl:last-child,
table:last-child,
blockquote:last-child { margin-bottom: 0; }

/* Mobile */

$breakpoint-mobile {
	h1,
	h2 { font-size: calc(1.5 * $font-size-base); }
}

/* Small Mobile */

$breakpoint-mobile-small {
	body { font-size: calc(0.875 * $font-size-base) !important; }

	h1,
	h2 { font-size: calc(1.25 * $font-size-base); }
}

@media only screen and ( max-width: 320px ) and ( max-height: 400px ) {
	h1,
	h2 { font-size: calc(1.1 * $font-size-base); }
}
